<template>
  <div class="resource-detail">
    <div class="detail-header">
      <button class="back-button" @click="$emit('back')"></button>
      <h1>{{ title }}</h1>
    </div>
    <img :src="titleImageUrl" :alt="title" class="detail-image">
    <div class="content markdown-content" v-html="content"></div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it';

export default {
  name: 'ResourceDetail',
  props: {
    resource: {
      type: Object,
      required: true
    }
  },
  
  data() {
    return {
      content: '',
      title: this.resource.title,
      titleImageUrl: this.resource.titleImageURL,
      md: null,
    }
  },

  created() {
    this.md = new MarkdownIt({
      html: true,
      // 
      replaceRules: {
        image(md) {
          md.renderer.rules.image = function(tokens, idx) {
            const token = tokens[idx];
            const srcIndex = token.attrIndex('src');
            const src = token.attrs[srcIndex][1];
            const alt = token.content || '';

            return `<div class="markdown-image-wrapper"><img src="${src}" alt="${alt}" /></div>`;
          };
        }
      }
    });
  },

  mounted() {
    this.fetchContent(this.resource.contentURL);
  },

  methods: {
    async fetchContent(contentURL) {
      try {
        const response = await fetch(contentURL);
        const parseContent = await response.text();
        this.content = this.md.render(parseContent);
      } catch(error) {
        console.log(error);
      }
      return this.content;
    }
  }
}
</script>

<style scoped>
.resource-detail {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.detail-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.back-button {
  padding: 8px 16px;
  margin-right: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
}

.back-button:hover {
  background-color: #f5f5f5;
}

.detail-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

/*  :deep()  */

.content {
  line-height: 1.8;
  color: #333;
}

.content :deep(p) {
  margin: 1em 0;
  text-align: left;
}

.content :deep(.markdown-image-wrapper) {
  text-align: center;
  margin: 1em 0;
}

.content :deep(.markdown-image-wrapper img) {
  max-width: 100%;
  height: auto;
}
</style>